"use client";

import * as React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ShoppingBag, X } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { cn } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";

export function Navigation() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = React.useState(false);
    const [scrolled, setScrolled] = React.useState(false);
    const pathname = usePathname();
    const { cartCount, setIsCartOpen } = useCart();

    React.useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    React.useEffect(() => {
        setIsMobileMenuOpen(false);
    }, [pathname]);

    const navLinks = [
        { name: "Home", href: "/" },
        { name: "Collection", href: "/products" },
        { name: "About", href: "/#about" },
        { name: "Contact", href: "/#contact" },
    ];

    return (
        <>
            <nav className={cn(
                "fixed left-0 right-0 z-50 transition-all duration-500",
                "top-[2.625rem]",
                scrolled
                    ? "bg-white/90 backdrop-blur-xl border-b border-gray-100 py-3 shadow-sm"
                    : "bg-transparent py-5"
            )}>
                <div className="container-custom flex items-center justify-between relative">
                    {/* Mobile Menu Btn */}
                    <button
                        className="flex md:hidden flex-col gap-1.5 p-2 -ml-2 transition-transform active:scale-95"
                        onClick={() => setIsMobileMenuOpen(true)}
                        aria-label="Toggle menu"
                    >
                        <span className="block w-6 h-0.5 bg-black" />
                        <span className="block w-6 h-0.5 bg-black" />
                        <span className="block w-6 h-0.5 bg-black" />
                    </button>

                    {/* Desktop Links (Left) */}
                    <div className="hidden md:flex flex-1 items-center gap-10">
                        {navLinks.map((link) => (
                            <Link
                                key={link.name}
                                href={link.href}
                                className="group relative text-[11px] font-bold text-gray-500 hover:text-black tracking-[0.2em] uppercase transition-colors"
                            >
                                {link.name}
                                <span className="absolute -bottom-1 left-0 w-0 h-px bg-black transition-all duration-300 group-hover:w-full" />
                            </Link>
                        ))}
                    </div>

                    {/* Logo (Centered) */}
                    <Link
                        href="/"
                        className="font-display font-black text-3xl tracking-tighter hover:opacity-70 transition-opacity absolute left-1/2 -translate-x-1/2"
                    >
                        intru
                    </Link>

                    {/* Actions (Right) */}
                    <div className="flex flex-1 items-center justify-end gap-6">
                        <Link
                            href="/account"
                            className="hidden md:block text-[11px] font-bold text-gray-500 hover:text-black tracking-[0.2em] uppercase transition-colors"
                        >
                            Account
                        </Link>
                        <button
                            onClick={() => setIsCartOpen(true)}
                            className="flex items-center gap-1.5 p-2 text-black transition-transform active:scale-95 relative group"
                            aria-label="Shopping cart"
                        >
                            <span className="hidden md:inline text-[11px] font-bold uppercase tracking-[0.2em] text-gray-500 group-hover:text-black transition-colors">Bag</span>
                            <ShoppingBag className="w-5 h-5 stroke-[1.5]" />
                            {cartCount > 0 && (
                                <span className="absolute -top-0.5 -right-0.5 bg-black text-white text-[9px] font-black w-4 h-4 flex items-center justify-center rounded-full">
                                    {cartCount}
                                </span>
                            )}
                        </button>
                    </div>
                </div>
            </nav>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {isMobileMenuOpen && (
                    <>
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-[100]"
                            onClick={() => setIsMobileMenuOpen(false)}
                        />
                        <motion.div
                            initial={{ x: "-100%" }}
                            animate={{ x: 0 }}
                            exit={{ x: "-100%" }}
                            transition={{ type: "spring", damping: 30, stiffness: 300 }}
                            className="fixed top-0 left-0 bottom-0 w-[85%] max-w-[400px] bg-white z-[101] shadow-2xl flex flex-col p-8"
                        >
                            <div className="flex justify-between items-center mb-16">
                                <span className="font-display font-black text-2xl tracking-tighter">intru</span>
                                <button onClick={() => setIsMobileMenuOpen(false)} className="p-2 -mr-2">
                                    <X className="w-8 h-8 stroke-[1]" />
                                </button>
                            </div>
                            <div className="flex flex-col space-y-8">
                                {navLinks.map((link, i) => (
                                    <motion.div
                                        key={link.name}
                                        initial={{ opacity: 0, x: -20 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        transition={{ delay: 0.1 + (i * 0.05) }}
                                    >
                                        <Link
                                            href={link.href}
                                            className="text-4xl font-bold uppercase tracking-tighter"
                                            onClick={() => setIsMobileMenuOpen(false)}
                                        >
                                            {link.name}
                                        </Link>
                                    </motion.div>
                                ))}
                            </div>
                            <div className="mt-auto space-y-8">
                                <button className="w-full bg-black text-white py-6 rounded-full text-xs font-black uppercase tracking-widest shadow-xl">
                                    Sign In
                                </button>
                                <div className="flex justify-center gap-8">
                                    <a href="https://www.instagram.com/intru.in/" target="_blank" rel="noopener noreferrer" className="text-[10px] font-bold uppercase tracking-widest text-neutral-400">Instagram</a>
                                    <a href="#" className="text-[10px] font-bold uppercase tracking-widest text-neutral-400">WhatsApp</a>
                                </div>
                            </div>
                        </motion.div>
                    </>
                )}
            </AnimatePresence>
        </>
    );
}
